<template>
    <div class="wrap">
        <div class="qna_back_img" v-show="!result">
            <img src="@/assets/background.png" class="back" >
            <img src="@/assets/flowers.png" class="flower">
            <div class="inner_area" v-show="item.display" v-for="(item,index) in qna_list" v-bind:key="index">
                <div class="back_btn_area">
                    <div class="back_btn" v-on:click="back(index)">
                        <img src="@/assets/back_btn.png">
                    </div>
                    <div class="step">
                        <p>{{item.num}}/12</p>
                    </div>
                </div>
                <div class="gaze_area">
                    <div class="gaze">
                        <div class="fill" :class="'gaze_'+item.num.toString()"></div>
                    </div>
                    <div class="icon" :class="'gaze_'+item.num.toString()">
                        <img src="@/assets/gaze_airplane.png">
                    </div>
                </div>
                <div class="qna_area">
                    <h1 class="num">Q{{item.num}}.</h1>
                    <p class="txt">{{item.qna_1}}<br>{{item.qna_2}}</p>
                </div>
                <ul class="answer_area">
                    <li class="one" v-on:click="ans_check(item.ans1_mbit,index)">{{item.ans1}}</li>
                    <li class="two" v-on:click="ans_check(item.ans2_mbit,index)">{{item.ans2}}</li>
                </ul>
            </div>
        </div>
        <div class="res_back_img" v-show="result">
            <img src="@/assets/loading.gif?v=2">
        </div>
    </div>
</template>

<script>
export default {
    name:"QnaView",
    data() {
        return {
            mbti_arr : ['INTP','ENTP','ENTJ','INTJ','INFJ','INFP','ISTJ','ISFJ','ISFP','ESFP','ESFJ','ENFP','ENFJ','ISTP','ESTP','ESTJ'],
            res_mbit:[],
            qna_list:[
                {
                    num:1,
                    qna_1 : '설레는 마음으로 여행 계획을 세운다면?',
                    qna_2 : '',
                    ans1 : '일정, 장소, 경비, 동선까지 세밀하게 계획해야 한다.',
                    ans1_mbit : 'j',
                    ans2 : '큰 틀만 잡은 후 나머지는 여행지에서 결정한다.',
                    ans2_mbit : 'p',
                    display : true
                },
                {
                    num:2,
                    qna_1 : '당신이 원하는 여행 스타일은?',
                    qna_2 : '',
                    ans1 : '삶에 지친 나, 바다를 바라보며 누워서 힐링 한번 해볼까? ',
                    ans1_mbit : 'i',
                    ans2 : '바다를 보면 풍덩! 서핑도 하고 보트도 탈 거야! ',
                    ans2_mbit : 'e',
                    display : false
                },
                {
                    num:3,
                    qna_1 : '당신이 원하는 숙소는?',
                    qna_2 : '',
                    ans1 : '새로운 사람은 불편해~ 프라이빗한 호텔이 좋아! ',
                    ans1_mbit : 'i',
                    ans2 : '새로운 인연을 만날 수 있는 게스트하우스가 좋아! ',
                    ans2_mbit : 'e',
                    display : false
                },
                {
                    num:4,
                    qna_1 : '여행을 가기 위한 짐을 챙길 때 당신은? ',
                    qna_2 : '',
                    ans1 : '체크리스트를 작성해서 빠진 것은 없는지 차근차근 준비한다.',
                    ans1_mbit : 'j',
                    ans2 : '출발하기 전날 밤 서둘러서 짐을 싼다. ',
                    ans2_mbit : 'p',
                    display : false
                },
                {
                    num:5,
                    qna_1 : '여행 가기 하루 전 잠자리에 들기 전 당신은?',
                    qna_2 : '',
                    ans1 : '설레는 마음으로 숙소와 비행기 시간 등 한번 더 확인하고 빨리 잔다. ',
                    ans1_mbit : 's',
                    ans2 : '여행 가면 뭐 하고 놀지 생각하거나, 사고가 나면 어떻게 하지 등 온갖 상상을 하다가 잠든다. ',
                    ans2_mbit : 'n',
                    display : false
                },
                {
                    num:6,
                    qna_1 : '여행지에서 아름다운 건축물을 보았을 때 나의 반응은?',
                    qna_2 : '',
                    ans1 : '“어떻게 이런 건물을 지었을까? 분석하여 본다. ',
                    ans1_mbit : 't',
                    ans2 : '“이렇게 아름다운 건물을 지었다니 놀라워!” 하며 감탄한다. ',
                    ans2_mbit : 'f',
                    display : false
                },
                {
                    num:7,
                    qna_1 : '여행 중 장사꾼이 나에게 필요 없는 제품을 사달라고 간절히 부탁한다면?' ,
                    qna_2 : '',
                    ans1 : '나에게 필요 없는 물건이니 솔직하게 거절한다. ',
                    ans1_mbit : 't',
                    ans2 : '장사꾼의 사정이 딱해 보여 그냥 한 개 산다. ',
                    ans2_mbit : 'f',
                    display : false
                },
                {
                    num:8,
                    qna_1 : '친구가 엑티비티 한 활동을 하자고 하면?',
                    qna_2 : '',
                    ans1 : '엑티비티한 경험보다 숙소에서 휴식하거나 여행지 구경하면서 힐링하는 게 좋아! ',
                    ans1_mbit : 'i',
                    ans2 : '여행 가면 필수코스~ 당연히 해야지! 뭐할까?',
                    ans2_mbit : 'e',
                    display : false
                },
                {
                    num:9,
                    qna_1 : '오늘 저녁은 친구들과 숙소에서 직접 만들어 먹기로 하였다. 당신의 요리 스타일은?',
                    qna_2 : '',
                    ans1 : '레시피에 있는 그대로 꼼꼼히 순서와 정량을 맞춰 요리한다.',
                    ans1_mbit : 's',
                    ans2 : '레시피는 참고만 하고 감으로 요리를 하며 완성한다. ',
                    ans2_mbit : 'n',
                    display : false
                },
                {
                    num:10,
                    qna_1 : '친구가 여권을 잃어버려 당황해하고 있다면?',
                    qna_2 : '',
                    ans1 : '어디서 어떻게 잃어버렸는지 확인하는 한편 대사관에 연락하는 등 해결책을 찾는다. ',
                    ans1_mbit : 't',
                    ans2 : '당황해하는 친구의 등을 토닥여주며 괜찮을 거라고 진정시킨다.',
                    ans2_mbit : 'f',
                    display : false
                },
                {
                    num:11,
                    qna_1 : '다음 여행지로 가는 방법이 두 가지가 있다. 당신은 어떤 방법으로 갈 것인가?',
                    qna_2 : '',
                    ans1 : '이전에 가봤던 알고 있는 길로 빠르게 간다.',
                    ans1_mbit : 's',
                    ans2 : '아는 길로만 가는 것은 재미없어. 새로운 길을 찾아간다.',
                    ans2_mbit : 'n',
                    display : false
                },
                {
                    num:12,
                    qna_1 : '다음 여행지는 석양이 아름다운 해변! 그러나 예상치 못한 비로 인해 석양을 볼 수 없을 때 당신은?',
                    qna_2 : '',
                    ans1 : '속상하지만 이럴 경우를 대비한 플랜 B인 다른 장소로 이동한다.',
                    ans1_mbit : 'j',
                    ans2 : '계획대로 되지 않는 것도 여행의 낭만이지~ 빗소리를 즐기며 느긋하게 다음 일정을 생각한다.',
                    ans2_mbit : 'p',
                    display : false
                },

            ],
            result:false,
        }
    },
    methods:{
        back:function(num){
            if(num==0){
                this.$router.go(-1);
            }else{
                var list = this.qna_list;
                for(var item of list){
                    item.display=false;
                }
                list[num-1].display=true;
                this.res_mbit=this.res_mbit.slice(0,num-1);
            }
        },
        res_calc:function(){
            var res_list = this.res_mbit;
            var res_obj={
                e:0,i:0,s:0,t:0,j:0,p:0,n:0,f:0,
            }
            for(var res of res_list){
                switch (res) {
                    case 'i':
                        res_obj.i++;
                        break;
                    case 'e':
                        res_obj.e++;
                        break;
                    case 's':
                        res_obj.s++;
                        break;
                    case 't':
                        res_obj.t++;
                        break;
                    case 'f':
                        res_obj.f++;
                        break;
                    case 'p':
                        res_obj.p++;
                        break;
                    case 'n':
                        res_obj.n++;
                        break;
                    case 'j':
                        res_obj.j++;
                        break;
                    default:
                        break;
                }
            }
            var res_txt="";
            if(res_obj.i>res_obj.e){
                res_txt+="I";
            }else if(res_obj.i<res_obj.e){
                res_txt+="E";
            }
            if(res_obj.s>res_obj.n){
                res_txt+="S";
            }else if(res_obj.s<res_obj.n){
                res_txt+="N";
            }
            if(res_obj.t>res_obj.f){
                res_txt+="T";
            }else if(res_obj.t<res_obj.f){
                res_txt+="F";
            }
            if(res_obj.j>res_obj.p){
                res_txt+="J";
            }else if(res_obj.j<res_obj.p){
                res_txt+="P";
            }
            var mbti_arr=this.mbti_arr;
            var idx=0;
            for(var item of mbti_arr){
                if(item==res_txt){
                    break;
                }
                idx++;
            }
            setTimeout(() => {
                this.$router.push('/result/'+idx);
            }, 2000);
            console.log(res_txt);
        },
        ans_check:function(mbti,num){
            var list = this.qna_list;
            for(var item of list){
                item.display=false;
            }
            this.res_mbit.push(mbti);
            if(num+1==12){
                this.result=true;
                this.res_calc();
            }else{
                list[num+1].display=true;
            }
        }
    }
}
</script>

