            height: 20px;
            float: left;
            position: relative;
            margin-top: 15px;
            &>.gaze{
                width: 100%;
                height: 20px;
                float: left;
                border-radius: 10px;
                border: 1px solid #fff;
                box-sizing: border-box;
                overflow: hidden;
                &>.fill{
                    width: 20%;
                    height: 100%;
                    float: left;
                    background-color: #fff;
                    &.gaze{
                        @for $i from 1 to 13{
                            &_#{$i}{
                                width: calc(100% * ($i/12));
                            }
                        }
                        
                    }
                    
                }
            }
            &>.icon{
                width: 35px;
                height: auto;
                float: left;
                position: absolute;
                top: calc(100% + 10px);
                transform: translateX(-17.5px);
                &.gaze{
                    @for $i from 1 to 13{
                        &_#{$i}{
                            left: calc(100% * ($i/12));
                        }
                    }
                }
                img{
                    width: 100%;
                    height: auto;
                    float: left;
                }
            }
        }
