            }
            &>.txt{
                width: 100%;
                height: auto;
                float: left;
                font-size: 40px;
                font-weight: 600;
            }
        }
        .answer_area{
            width: 100%;
            height: auto;
            float: left;
            padding: 0 15px;
            box-sizing: border-box;
            margin-top: 70px;
            li{
                width: 100%;
                height: auto;
                float: left;
                font-size: 30px;
                line-height: 1;
                margin-bottom: 30px;
                padding-left: 30px;
                box-sizing: border-box;
                position: relative;
                font-weight: 600;
                &.one{
                    &::before{
                        content: "①";
                        clear: both;
                        position: absolute;
                        left: 0;
                        top: 0;
                    }
                }
                &.two{
                    &::before{
                        content: "②";
                        clear: both;
                        position: absolute;
                        left: 0;
                        top: 0;
                    }
                }
                
                &:last-child{
                    margin-bottom: 0;
                }
            }
        }
    }
}
